article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0; }

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.app {
  min-height: 100vh;
  margin: 0 auto;
  width: 800px;
  font-family: 'Open Sans', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center; }

.button button {
  background: navy;
  color: white;
  text-align: center;
  border: none;
  display: inline-block;
  width: 100%;
  padding: 10px 10px;
  margin: 10px 0; }

.question {
  text-align: center; }

.input {
  display: flex;
  margin-right: 10px;
  margin-bottom: 10px; }
  .input__checkBox label {
    padding-right: 10px;
    font-size: 12px; }
  .input__checkBox--addSpaceBelow {
    margin-bottom: 20px; }
  .input__radio {
    display: flex;
    align-items: center;
    height: 55px; }
    .input__radio label {
      padding-right: 10px;
      padding-bottom: 20px; }
    .input__radio input {
      margin-right: 20px;
      margin-bottom: 20px; }
  .input__text {
    flex-direction: column; }
    .input__text--toEnd {
      margin-right: 0; }

.whoIsGettingVaccinated {
  display: flex;
  align-items: center;
  flex-direction: column; }
  .whoIsGettingVaccinated__answerOptions {
    width: 300px; }

.screening__name {
  display: flex;
  padding: 0 10px; }

.screening__columnHeadings {
  border-bottom: 1px solid black;
  display: flex;
  background: navy;
  color: white;
  text-transform: uppercase; }

.screening__title {
  width: 80%;
  padding: 0 10px; }

.screening__inputTitles {
  width: 20%;
  display: flex;
  justify-content: space-around;
  align-items: center; }

.screening__questions {
  border: 1px solid black;
  border-bottom: none; }

.screening__byPass {
  display: flex;
  justify-content: flex-end; }

.screening__byPassOption {
  min-width: 20%;
  margin-left: 10px; }

.screeningQuestion {
  display: flex; }
  .screeningQuestion__question {
    border-bottom: 1px black solid;
    width: 80%;
    padding: 0 10px; }
  .screeningQuestion__answerOptions {
    border-bottom: 1px black solid;
    border-left: 1px black solid;
    width: 20%;
    display: flex;
    justify-content: space-around;
    align-items: center; }

.infoCollector__section {
  display: flex; }
  .infoCollector__section--column {
    flex-direction: column; }
  .infoCollector__section p {
    margin-bottom: 0; }

.infoCollector__sectionHeading p {
  margin: 0px; }

.infoCollector__infoItem {
  width: 50%; }
  .infoCollector__infoItem--radioContainer {
    display: flex; }
